.order-container {
    font-family: Arial, sans-serif;
    background-color: #1c1c2b;
    color: white;
    padding: 20px;
    border-radius: 8px;
    max-width: 400px;
    margin: auto;
}

.order-header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.order-back-button {
    background: none;
    border: none;
    color: #fff;
    font-size: 1.5em;
    cursor: pointer;
    margin-right: 10px;
}

.order-title {
    font-size: 1.5em;
    margin: 0;
}

.order-empty {
    text-align: center;
    font-size: 1.2em;
    color: #bbb;
}

.order-list {
    list-style: none;
    padding: 0;
    margin: 0 0 20px;
}

.order-item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    padding-bottom: 10px;
    border-bottom: 1px solid #333;
}

.order-item-image {
    width: 50px;
    height: 50px;
    border-radius: 5px;
    margin-right: 15px;
}

.order-item-info {
    display: flex;
    justify-content: space-between;
    flex: 1;
    align-items: center;
}

.order-item-name {
    font-size: 1em;
    font-weight: bold;
    color: white;
    flex: 1;
}

.order-item-controls {
    display: flex;
    align-items: center;
    gap: 5px;
}

.order-item-button {
    background-color: #444;
    color: white;
    border: none;
    border-radius: 3px;
    width: 25px;
    height: 25px;
    text-align: center;
    font-size: 1.2em;
    cursor: pointer;
}

.order-item-button:hover {
    background-color: #555;
}

.order-item-quantity {
    font-size: 1em;
    font-weight: bold;
    color: #f39c12;
    margin: 0 5px;
}

.order-item-price {
    font-size: 1em;
    font-weight: bold;
    color: #4caf50;
}

.order-summary {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 20px;
}

.order-summary span {
    font-size: 1.2em;
}

.order-comment {
    width: 100%;
    height: 50px;
    border: none;
    border-radius: 5px;
    padding: 10px;
    font-size: 1em;
    color: #333;
}

.order-pay-button {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 15px;
    font-size: 1.2em;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
}

.order-pay-button:hover {
    background-color: #45a049;
}
